import React, { Component } from 'react'
import { Tabs, Tab, Grid, Cell, Card,CardText, CardTitle, CardActions, Button, CardMenu, IconButton, Spinner } from 'react-mdl' ;
// import { url } from 'inspector';
class Projct extends Component{
    constructor(props){
        super(props)
        this.state = { activeTab: 0 };
        
    }
    toggleCategories(){
    if(this.state.activeTab === 0){
        return(
            //  project 1
            <div className="row projects-grid ">
            <div className="col-lg-4 col-md-4 col-12">
            <Card shadow={5} style={{minWidth: '450' , margin: 'auto'}}>
            <CardTitle style={{color: '#fff', height: '176px' , background: 'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/ cover'}}>
            React Project #1
            </CardTitle>
            <CardText>
            My tourism Guide app . In which i show information and full detailed related to 
             beautiful places of the pakistan.   ........
            </CardText>
            <CardActions style={{border: '1px solid silver'}}>
            <Button colored href="https://github.com/hasniansaleem/pakistanbeautifulplaces">GitHub </Button>
           
            </CardActions>
            <CardMenu style={{color: '#fff'}}>
            <IconButton name="share" />
            </CardMenu>
            </Card>
            </div>

            {/* // project 2 */}

            <div className="col-lg-4 col-md-4 col-12">
                <Card shadow={5} style={{minWidth: '450' , margin: 'auto'}}>
                <CardTitle style={{color: '#fff', height: '176px' , background: 'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/ cover'}}>
                React Project #1
                </CardTitle>
                <CardText>
                    Hello this is Reactjs project in which i show ........
                </CardText>
                <CardActions style={{border: '1px solid silver'}}>
                <Button colored>GitHub </Button>
               
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                <IconButton name="share" />
                </CardMenu>
                </Card>
            </div>

            {/* // project 3 */}

            <div className="col-lg-4 col-md-4 col-12">
                <Card shadow={5} style={{minWidth: '450' , margin: 'auto'}}>
                <CardTitle style={{color: '#fff', height: '176px' , background: 'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/ cover'}}>
                React Projext #1
                </CardTitle>
                <CardText>
                    Hello this is Reactjs project in which i show ........
                </CardText>
                <CardActions style={{border: '1px solid silver'}}>
                <Button colored>GitHub </Button>
                
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                <IconButton name="share" />
                </CardMenu>
                </Card>
                </div>
                </div>
            )

    }
    if(this.state.activeTab === 1){
        return(
            // <ProgressBar indeterminate style={{paddingTop: '1rem' ,marginTop: '10em', marginLeft: '17em'}} />
            <Spinner singleColor style={{height: '150px', width: '150px', color: 'yellow', marginTop: '13em', marginLeft: '35em'}} />

          
        )
    }
    if(this.state.activeTab === 2){
        return(
            <Spinner  singleColor style={{height: '150px', width: '150px', color: 'yellow', marginTop: '13em', marginLeft: '35em'}} />

        )
    }
}
    

    render(){
        return(
   
            <div className="category                                                                                       -tabs">
            <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                <Tab>React</Tab>
                <Tab>Html/Css</Tab>
                <Tab>NodeJs</Tab>
            </Tabs>
            
            <Grid >
            <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
            </Cell>
            </Grid>

        </div>   
        );
    }
}
export default Projct;