import React, { Component } from 'react'
import {Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import { Link } from 'react-router-dom';
import Main from './main'
class Materailui extends Component{
    render(){
        return(
            <div style={{height: '600px', position: 'relative'}}>
    <Layout fixedHeader>
        {/* <Header className="header-color" title={<span><span style={{ color: '#ffff' }}>Muhammad  </span><strong>Hasnain</strong></span>}> */}
           <Header className="header-color" title={<Link style={{textDecoration: 'none', color: 'white'}} to="/">
        Myportfolio </Link> }
        scroll>  

       
            <Navigation>
                <Link to="/">Home</Link>
                <Link to="/resume">Resume</Link>
                <Link to="/abouteme">Aboutme</Link>
                <Link to="/project">Project</Link>
                <Link to="/contact">Contact</Link>
            </Navigation>
        </Header>
        <Drawer  title={<Link style={{textDecoration: 'none', color: 'black'}} to="/">
        Myportfolio </Link> }>
     
        {/* side bar Navigation code */}
            <Navigation>
                <Link to="/">Home</Link>
            <Link to="/resume">Resume</Link>
                <Link to="/abouteme">Aboutme</Link>
                <Link to="/project">Project</Link>
                <Link to="/contact">Contact</Link>
            </Navigation>
        </Drawer>
        <Content >
            <div className="page-content"/>
            <Main/>
            
            </Content>
    </Layout>
</div>

        );
    }
}

export default Materailui;